.slide-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #0a0a0a;
}

.slide-wrapper {
  width: 100%;
  height: 100%;
}

.slide-nav {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
  pointer-events: none;
  z-index: 100;
}

.nav-button {
  pointer-events: all;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(26, 26, 26, 0.6);
  backdrop-filter: blur(10px);
  color: #f5f5f5;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.nav-button:hover {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  transform: scale(1.1);
}

.nav-button:active {
  transform: scale(0.95);
}

.slide-indicators {
  position: fixed;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1rem;
  z-index: 100;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.indicator.active {
  background: #8b5cf6;
  border-color: #8b5cf6;
  width: 40px;
  border-radius: 6px;
}

.indicator:hover:not(.active) {
  background: rgba(255, 255, 255, 0.4);
  transform: scale(1.2);
}

@media (max-width: 768px) {
  .slide-nav {
    padding: 0 1rem;
  }

  .nav-button {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }

  .slide-indicators {
    bottom: 2rem;
    gap: 0.75rem;
  }

  .indicator {
    width: 10px;
    height: 10px;
  }

  .indicator.active {
    width: 30px;
  }
}
