.track-slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
  overflow-y: auto;
  padding: 2rem;
}

.track-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 30%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(59, 130, 246, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.track-content {
  max-width: 1200px;
  width: 100%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.track-header {
  text-align: center;
}

.artist-name {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 300;
  letter-spacing: 0.15em;
  margin: 0 0 0.5rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.artist-subtitle {
  font-size: clamp(0.875rem, 1.5vw, 1.125rem);
  letter-spacing: 0.3em;
  color: #a0a0a0;
  text-transform: uppercase;
  margin: 0;
  font-weight: 300;
}

.track-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.album-art {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  border-radius: 8px;
  overflow: hidden;
  box-shadow:
    0 20px 60px rgba(0, 0, 0, 0.5),
    0 0 40px rgba(139, 92, 246, 0.2);
}

.album-art img {
  width: 100%;
  height: auto;
  display: block;
}

.track-info {
  text-align: left;
  padding: 2rem;
}

.track-title {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 300;
  letter-spacing: 0.05em;
  margin: 0 0 1.5rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.track-description {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  line-height: 1.8;
  color: #c0c0c0;
  margin: 0;
  font-weight: 300;
}

.streaming-platforms {
  text-align: center;
}

.platforms-label {
  font-size: 1.125rem;
  color: #a0a0a0;
  margin: 0 0 1.5rem 0;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-weight: 300;
}

.platforms-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
  max-width: 900px;
  margin: 0 auto;
}

.platform-button {
  padding: 1rem 2rem;
  background: rgba(26, 26, 26, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: #f5f5f5;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.05em;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}

.platform-button:hover {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  box-shadow: 0 8px 24px rgba(139, 92, 246, 0.2);
}

@media (max-width: 968px) {
  .track-slide {
    padding: 1rem;
  }

  .track-content {
    gap: 2rem;
  }

  .track-main {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .track-info {
    text-align: center;
    padding: 1rem;
  }

  .platforms-grid {
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 0.75rem;
  }

  .platform-button {
    padding: 0.875rem 1.5rem;
    font-size: 0.875rem;
  }
}

@media (max-width: 480px) {
  .platforms-grid {
    grid-template-columns: 1fr 1fr;
  }
}
