.world-record-slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
  overflow-y: auto;
  padding: 2rem;
}

.world-record-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 50% 50%, rgba(139, 92, 246, 0.15) 0%, transparent 60%);
  pointer-events: none;
}

.record-content {
  max-width: 1200px;
  width: 100%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.record-header {
  text-align: center;
}

.record-label {
  display: inline-block;
  margin-bottom: 1rem;
}

.record-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.label-text {
  display: inline-block;
  padding: 0.75rem 2rem;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  color: #a78bfa;
  font-size: 0.875rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  border-radius: 30px;
  font-weight: 400;
}

.record-title {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 300;
  letter-spacing: 0.05em;
  margin: 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.record-hero {
  width: 100%;
}

.hero-placeholder {
  width: 100%;
  aspect-ratio: 16 / 9;
  background: rgba(26, 26, 26, 0.5);
  border: 2px solid rgba(139, 92, 246, 0.2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  overflow: hidden;
}

.hero-placeholder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-image {
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.hero-image img {
  width: 100%;
  height: auto;
  display: block;
}

.placeholder-icon {
  font-size: 6rem;
  opacity: 0.3;
}

.record-details {
  width: 100%;
  text-align: left;
  padding: 2rem;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.detail-label {
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #808080;
  font-weight: 300;
}

.detail-value {
  font-size: clamp(1.5rem, 3vw, 2rem);
  color: #8b5cf6;
  font-weight: 400;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.detail-divider {
  height: 1px;
  width: 100px;
  background: linear-gradient(90deg, #8b5cf6, transparent);
  margin: 2rem 0;
}

.record-description {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  line-height: 1.8;
  color: #c0c0c0;
  margin: 0;
  font-weight: 300;
  text-align: left;
}

@media (max-width: 968px) {
  .world-record-slide {
    padding: 1rem;
  }

  .record-content {
    gap: 2rem;
  }

  .record-layout {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .record-details {
    padding: 1rem;
    text-align: center;
  }

  .record-description {
    text-align: center;
  }

  .detail-divider {
    margin: 2rem auto;
    background: linear-gradient(90deg, transparent, #8b5cf6, transparent);
  }

  .placeholder-icon {
    font-size: 4rem;
  }
}
