.teacher-slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
  overflow-y: auto;
  padding: 2rem;
}

.teacher-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 50% 50%, rgba(59, 130, 246, 0.12) 0%, transparent 60%);
  pointer-events: none;
}

.teacher-content {
  max-width: 1200px;
  width: 100%;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.teacher-header {
  text-align: center;
}

.teacher-label {
  display: inline-block;
  margin-bottom: 1rem;
}

.teacher-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.teacher-name {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 300;
  letter-spacing: 0.08em;
  margin: 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.teacher-title {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  letter-spacing: 0.2em;
  color: #a0a0a0;
  text-transform: uppercase;
  margin: 0;
  font-weight: 300;
}

.teacher-hero {
  width: 100%;
}

.teacher-hero .hero-image {
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.teacher-hero .hero-image img {
  width: 100%;
  height: auto;
  display: block;
}

.gratitude-message {
  width: 100%;
  padding: 2rem;
}

.message-divider {
  height: 1px;
  width: 150px;
  background: linear-gradient(90deg, #3b82f6, transparent);
  margin: 0 0 2rem 0;
}

.message-text {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  line-height: 1.9;
  color: #c0c0c0;
  margin: 0 0 2rem 0;
  font-weight: 300;
  text-align: left;
}

.message-signature {
  font-size: clamp(1rem, 1.2vw, 1.125rem);
  color: #8b5cf6;
  font-style: italic;
  margin: 0;
  font-weight: 300;
}

@media (max-width: 968px) {
  .teacher-slide {
    padding: 1rem;
  }

  .teacher-content {
    gap: 2rem;
  }

  .teacher-layout {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .gratitude-message {
    padding: 1rem;
    text-align: center;
  }

  .message-text {
    text-align: center;
    margin: 0 0 1.5rem 0;
  }

  .message-divider {
    width: 100px;
    margin: 0 auto 1.5rem auto;
    background: linear-gradient(90deg, transparent, #3b82f6, transparent);
  }
}
